import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import CloudUploadIcon from "@material-ui/icons/CloudUpload";
import { FormikProps } from "formik";
import React from "react";
import { IDftFilesReuseFMO } from "../types";
import StyledSelect from "./StyledSelect";
import SubmitButton from "./SubmitButton";

const styles = (theme: Theme) =>
  createStyles({
    formControl: {
      margin: theme.spacing.unit
    }
  });

const loadOptions = (prevUsedFiles: any) => (
  inputValue: string,
  callback: (options: Array<{ value: number; label: string }>) => void
) => {
  if (Array.isArray(prevUsedFiles)) {
    const uploads = {};
    const uniqueFiles = prevUsedFiles.filter((file: any) => {
      if (uploads[file.upload]) {
        return false;
      }
      uploads[file.upload] = true;
      return true;
    });
    callback(
      uniqueFiles.map((file: any) => ({
        label: file.original_name,
        value: file.pk
      }))
    );
  }
};

export interface IDftFilesReuseFMOProps
  extends FormikProps<IDftFilesReuseFMO>,
    WithStyles<typeof styles> {
  prevUsedFiles: any;
}

const DftFilesReuseFMO: React.SFC<IDftFilesReuseFMOProps> = ({
  classes,
  prevUsedFiles,
  ...props
}) => (
  <form onSubmit={props.handleSubmit}>
    <StyledSelect
      async={true}
      errors={props.errors.reusedFile}
      label="File to Reuse"
      loadOptions={loadOptions(prevUsedFiles)}
      name="reusedFile"
      onBlur={props.handleBlur}
      setFieldValue={props.setFieldValue}
    />
    <SubmitButton
      error={Object.keys(props.errors).length !== 0}
      isSubmitting={props.isSubmitting}
      buttonText="Submit"
      ButtonIcon={CloudUploadIcon}
    />
  </form>
);
export default withStyles(styles)(DftFilesReuseFMO);



