import Button from "@material-ui/core/Button";
import CircularProgress from "@material-ui/core/CircularProgress";
import Hidden from "@material-ui/core/Hidden";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import React from "react";
import { NOOP_FUNCTION } from "../constants";
import Loading from "./Loading";
export const styles = (theme: Theme) =>
  createStyles({
    fab: {
      bottom: theme.spacing.unit * 2,
      position: "absolute",
      right: theme.spacing.unit * 2
    },
    rightIcon: {
      marginLeft: theme.spacing.unit
    }
  });

export interface ISubmitButtonProps extends WithStyles<typeof styles> {
  error?: boolean;
  isSubmitting: boolean;
  buttonText: string;
  ButtonIcon: any;
}
const SubmitButton: React.SFC<ISubmitButtonProps> = ({
  classes,
  error,
  isSubmitting,
  buttonText,
  ButtonIcon
}) => {
  if (!isSubmitting) {
    return (
      <div>
        <Hidden smUp={true}>
          <Button
            aria-label={buttonText}
            color="primary"
            type="submit"
            variant="fab"
            disabled={isSubmitting || error}
            className={classes.fab}
          >
            <ButtonIcon />
          </Button>
        </Hidden>
        <Hidden xsDown={true}>
          <Button
            aria-label={buttonText}
            color="primary"
            type="submit"
            variant="extendedFab"
            disabled={isSubmitting || error}
            className={classes.fab}
          >
            {buttonText}
            <ButtonIcon className={classes.rightIcon} />
          </Button>
        </Hidden>
      </div>
    );
  } else {
    return (
      <Loading
        component={<CircularProgress className={classes.fab} />}
        error={false}
        isLoading={true}
        pastDelay={true}
        timedOut={false}
        retry={NOOP_FUNCTION}
      />
    );
  }
};
export default withStyles(styles)(SubmitButton);



