import FormControl from "@material-ui/core/FormControl";
import FormHelperText from "@material-ui/core/FormHelperText";
import FormLabel from "@material-ui/core/FormLabel";
import { Theme } from "@material-ui/core/styles/createMuiTheme";
import createStyles from "@material-ui/core/styles/createStyles";
import withStyles, { WithStyles } from "@material-ui/core/styles/withStyles";
import React from "react";
import Select from "react-select";
import AsyncSelect from "react-select/lib/Async";

export const styles = (theme: Theme) =>
  createStyles({
    formLabel: {
      marginBottom: theme.spacing.unit
    },
    root: {
      margin: theme.spacing.unit
      // zIndex: 2 // <-- might be needed if other components cover Select
    }
  });
export interface IStyledSelectProps extends WithStyles<typeof styles> {
  async?: boolean;
  loadOptions?: any;
  cacheOptions?: boolean;
  errors?: {};
  isMulti?: boolean;
  label: string;
  name: string;
  fullWidth?: boolean;
  options?:
    | Array<{
        value: string;
        label: string;
      }>
    | Array<{
        options: Array<{
          value: string;
          label: string;
        }>;
        label: string;
      }>;
  onBlur: any;
  required?: boolean;
  setFieldValue: any;
}
const handleChange = (name: string, setFieldValue: any, isMulti?: boolean) => (
  input: any
) => {
  if (isMulti) {
    setFieldValue(name, input.slice().map((option: any) => option.value));
  } else {
    setFieldValue(name, input.value);
  }
};
const StyledSelect: React.SFC<IStyledSelectProps> = ({
  classes,
  async,
  errors,
  label,
  loadOptions,
  cacheOptions = false,
  fullWidth = true,
  required = true,
  setFieldValue,
  ...props
}) => (
  <FormControl
    component="fieldset"
    fullWidth={fullWidth}
    required={required}
    error={!!errors}
    className={classes.root}
  >
    <FormLabel component="legend" className={classes.formLabel}>
      {label}
    </FormLabel>
    {async && (
      <AsyncSelect
        cacheOptions={cacheOptions}
        defaultOptions={true}
        loadOptions={loadOptions}
        onChange={handleChange(props.name, setFieldValue, props.isMulti)}
        {...props}
      />
    )}
    {!async && (
      <Select
        onChange={handleChange(props.name, setFieldValue, props.isMulti)}
        {...props}
      />
    )}
    {errors && <FormHelperText>{errors}</FormHelperText>}
  </FormControl>
);
export default withStyles(styles)(StyledSelect);



